*{ margin : 0; padding : 0; box-sizing: border-box;}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  padding: env(safe-area-inset);
  padding-top: constant(safe-area-inset-top);
  padding-top: env(safe-area-inset-top);
  background: #6D4DFF; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(top, #6D4DFF, #EB47DA); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom, #6D4DFF, #EB47DA); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(top, #6D4DFF, #EB47DA); /* For Firefox 3.6 to 15 */
  background: linear-gradient(to bottom, #6D4DFF, #EB47DA); /* Standard syntax */
  background: #6D4DFF;
  color: #8595A5;
  -webkit-overflow-scrolling: touch;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  overscroll-behavior: contain;
}

body.message-open {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: #6D4DFF; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(top, #6D4DFF, #EB47DA); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom, #6D4DFF, #EB47DA); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(top, #6D4DFF, #EB47DA); /* For Firefox 3.6 to 15 */
  background: linear-gradient(to bottom, #6D4DFF, #EB47DA); /* Standard syntax */
  background-color: rgba(0, 0, 0, 0.5);
}

a:hover {
  text-decoration: none;
}

.container {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

@media (max-width: 767.98px) {
  .container {
    width: 100%;
  } 
}

